body{max-width:50em;margin:0 auto}nav{margin:2em 0}nav ul{list-style-type:none;display:flex;width:100%;justify-content:space-around;margin:0;padding:0}.cat{width:50%;max-width:50%}.cat img{width:100%}.container{display:flex}.container>div{flex:0 0 50%}.meep-speak{font-size:2.25em;font-family:'Caveat Brush', cursive}#addForm input[type="text"]{width:100%;box-sizing:border-box;margin:0;font-size:2em}.pet-photo-container{height:150px;width:150px;overflow:hidden;display:flex;flex-flow:column nowrap;justify-content:center;border-radius:10px;background-size:cover;background-position:center center;flex-basis:0 0 150px}main>ul{margin:0;padding:0;list-style-type:none}main>ul li{display:flex;flex-flow:row nowrap;align-items:center;justify-content:space-between;margin:1em 0}
